import { Component,OnInit } from '@angular/core';
import { Project } from 'src/app/project';
import { ProjectsService } from '../../projects.service';


@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit 
{

  projects:Project[] = [];
  newProject:Project = new Project();
  editProject:Project = new Project();
  editIndex:any=null;
  deleteProject:Project = new Project();
  deleteIndex:any=null;
  searchBy:string="ProjectName";
  searchText:string="";

  constructor(private projectsservice:ProjectsService)
  {
  }

  ngOnInit()
  {
    this.projectsservice.getAllProjects().subscribe({
      next:(response:Project[]) =>{
        this.projects = response;
      },
      error:(error)=>{
        console.log(error);
        alert("Authentication Failed!");
      }
    }
    );
  }
  onSaveClick()
  {
    this.projectsservice.insertProject(this.newProject).subscribe({
      next:(response)=>
    {
      //Add project to Grid
      var p:Project = new Project()
      p.projectID=response.projectID;
      p.projectName=response.projectName;
      p.dateOfStart=response.dateOfStart;
      p.teamsize=response.teamsize;
    this.projects.push(p);
      //Clear textboxes
    this.newProject.projectID = null;
    this.newProject.projectName=null;
    this.newProject.dateOfStart=null;
    this.newProject.teamsize=null;
    },
    error:(error)=>
    {
      console.log(error);
    }
  }
  );
  }

  onEditclick(event:any,index:number)
  {
    this.editProject.projectID =this.projects[index].projectID;
    this.editProject.projectName =this.projects[index].projectName;
    this.editProject.dateOfStart =this.projects[index].dateOfStart;
    this.editProject.teamsize =this.projects[index].teamsize;
    this.editIndex =index;
  }
  onUpdateclick()
  {
    this.projectsservice.updateProject(this.editProject).subscribe((response:Project)=>
    {
      var p:Project = new Project()
      p.projectID=response.projectID;
      p.projectName=response.projectName;
      p.dateOfStart=response.dateOfStart;
      p.teamsize=response.teamsize;
      this.projects[this.editIndex] = p;


      this.editProject.projectID = null;
      this.editProject.projectName=null;
      this.editProject.dateOfStart=null;
      this.editProject.teamsize=null;
    })
  }

  onDeleteClick(event:any,index:number)
  {
    this.deleteIndex=index;
    this.deleteProject.projectID = this.projects[index].projectID;
    this.deleteIndex.projectName=this.projects[index].projectName;
    this.deleteIndex.dateOfStart=this.projects[index].dateOfStart;
    this.deleteIndex.teamsize=this.projects[index].teamsize;
  }
  onDeleteConfirmClick()
  {
    this.projectsservice.deleteProject(this.deleteProject.projectID).subscribe({
      next:(response)=>
      {
        this.projects.splice(this.deleteIndex,1);
        this.deleteProject.projectID=null;
        this.deleteProject.projectName=null;
        this.deleteProject.dateOfStart=null;
        this.deleteProject.teamsize=null;
      },
      error:(error)=>{
        console.log(error); 
      }
    });
  }


  onSearchClick()
  {
    this.projectsservice.SearchProjects(this.searchBy,this.searchText).subscribe({
      next:(response:Project[])=>{
        this.projects = response;
      },
      error:(error)=>{
        console.log(error); 
      }
    });
  }

}
